.avatar {
  width: 132px;
  height: 132px;
  overflow: hidden;

  border: 4px solid $blue-1;
  border-radius: 50%;

  &__img {
    width: 132px;
    height: 132px;
    margin-top: -4px;
    margin-left: -4px;

    @media (min-width: 768px) {
      width: 200px;
      height: 200px;
    }

    @media (min-width: 1200px) {
      width: 150px;
      height: 150px;
    }
  }

  @media (min-width: 768px) {
    width: 200px;
    height: 200px;
  }

  @media (min-width: 1200px) {
    width: 150px;
    height: 150px;
  }
}
