$gray-1: #58697a;
$gray-2: #959aa8;
$gray-3: #eff3f7;
$gray-4: #849db5;
$gray-5: #687b8f;
$black: #000;
$white: #fff;
$font-color: #4a545d;
$link-color: #89cbd9;
$bg-color-header: $gray-3;
$bg-color-footer: $gray-3;
$bg-color-main-nav-320: $gray-3;
$bg-color-main-nav-320-active: #2fa5bc;
$border-main-nav-320: #8196ac;
$skills-gradient-color-top: $blue-1;
$skills-gradient-color-bottom: darken($blue-1, 11);
